import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  Box,
  Card,
  CardContent,
  Typography,
  CircularProgress,
} from "@mui/material";

import VerifyEmailOtp from "./VerifyEmailOtp";
import CheckAgentStatus from "./CheckAgentStatus";
import CreateAgent from "./CreateAgent";

const IrctcComingSoon = () => {
  const navigate = useNavigate();
  const [loadingStatus, setLoadingStatus] = useState(true);
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [showOtp, setShowOtp] = useState(false);
  const [prefillData, setPrefillData] = useState(null);
  const handleStatusResult = (onboarded, data) => {
    setLoadingStatus(false);

    if (onboarded) {
      navigate("/agent-login");
      return;
    }

    setPrefillData(data);
    setShowCreateForm(true);
  };

  const handleCreateSuccess = () => {
    setShowCreateForm(false);
    setShowOtp(true);
  };

  const handleOtpSuccess = () => {
    navigate("/agent-login");
  };

  if (loadingStatus) {
    return <CheckAgentStatus onResult={handleStatusResult} />;
  }

  if (showCreateForm) {
    return (
      <CreateAgent onSuccess={handleCreateSuccess} prefillData={prefillData} />
    );
  }

  if (showOtp) {
    return <VerifyEmailOtp onVerified={handleOtpSuccess} />;
  }

  return (
    <Box
      sx={{
        minHeight: "70vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <Card sx={{ width: 420 }}>
        <CardContent sx={{ textAlign: "center" }}>
          <CircularProgress sx={{ mb: 2 }} />
          <Typography variant="h6">Preparing IRCTC accessâ€¦</Typography>
        </CardContent>
      </Card>
    </Box>
  );
};

export default IrctcComingSoon;
