import React, { useRef, useState, useEffect } from "react";
import {
  Box,
  Card,
  CardContent,
  Typography,
  TextField,
  Button,
  Stack,
  CircularProgress,
  Grow,
} from "@mui/material";
import MailOutlineIcon from "@mui/icons-material/MailOutline";
import VerifiedIcon from "@mui/icons-material/Verified";
import {
  verifyIrctcEmailOtp,
  resendIrctcEmailOtp,
} from "../../api/login/Login";

const OTP_LENGTH = 4;
const VerifyEmailOtp = ({ onVerified }) => {
  const [otp, setOtp] = useState(Array(OTP_LENGTH).fill(""));
  const [loading, setLoading] = useState(false);
  const [resendLoading, setResendLoading] = useState(false);
  const [error, setError] = useState("");
  const inputsRef = useRef([]);
  const handleChange = (value, index) => {
    if (!/^\d?$/.test(value)) return;
    const copy = [...otp];
    copy[index] = value;
    setOtp(copy);
    if (value && index < OTP_LENGTH - 1) {
      inputsRef.current[index + 1]?.focus();
    }
  };
  const handleKeyDown = (e, index) => {
    if (e.key === "Backspace" && !otp[index] && index > 0) {
      inputsRef.current[index - 1]?.focus();
    }
  };
  const handleVerify = async () => {
    const otpValue = otp.join("");
    if (otpValue.length !== OTP_LENGTH) return;
    setLoading(true);
    setError("");
    try {
      await verifyIrctcEmailOtp({ otp: otpValue });
      onVerified();
    } catch {
      setError("Invalid or expired OTP");
    } finally {
      setLoading(false);
    }
  };
  const handleResend = async () => {
    setResendLoading(true);
    setError("");
    try {
      await resendIrctcEmailOtp();
    } catch {
      setError("Failed to resend OTP");
    } finally {
      setResendLoading(false);
    }
  };
  const isComplete = otp.join("").length === OTP_LENGTH;
  useEffect(() => {
    inputsRef.current[0]?.focus();
  }, []);

  return (
    <Box
      sx={{
        minHeight: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        background:
          "linear-gradient(135deg, #0f2027 0%, #203a43 50%, #2c5364 100%)",
      }}
    >
      <Grow in>
        <Card sx={{ width: 420, borderRadius: 4 }}>
          <Box
            sx={{
              height: 6,
              background: "#cc9840",
            }}
          />
          <CardContent sx={{ p: 4 }}>
            <Stack alignItems="center" spacing={1} mb={3}>
              <MailOutlineIcon sx={{ fontSize: 40, color: "#cc9840" }} />
              <Typography variant="h6">Verify Email</Typography>
              <Typography variant="body2" color="text.secondary" align="center">
                Enter the 4-digit OTP sent to your email
              </Typography>
            </Stack>
            <Stack direction="row" spacing={1.5} justifyContent="center">
              {otp.map((v, i) => (
                <TextField
                  key={i}
                  inputRef={(el) => (inputsRef.current[i] = el)}
                  value={v}
                  onChange={(e) => handleChange(e.target.value, i)}
                  onKeyDown={(e) => handleKeyDown(e, i)}
                  inputProps={{
                    maxLength: 1,
                    style: {
                      textAlign: "center",
                      fontSize: "22px",
                      fontWeight: 600,
                    },
                  }}
                  sx={{
                    width: 56,
                    "& .MuiOutlinedInput-root": {
                      borderRadius: 2,
                      backgroundColor: "#fff",
                      "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                        borderColor: "#cc9840",
                        borderWidth: 2,
                      },
                    },
                  }}
                />
              ))}
            </Stack>
            {error && (
              <Typography
                color="error"
                align="center"
                sx={{ mt: 2, fontSize: 13 }}
              >
                {error}
              </Typography>
            )}
            <Button
              fullWidth
              variant="contained"
              sx={{
                mt: 3,
                py: 1.5,
                background: "#cc9840",
                "&.Mui-disabled": {
                  cursor: "not-allowed",
                  pointerEvents: "auto",
                },
              }}
              onClick={handleVerify}
              disabled={loading || !isComplete}
              endIcon={!loading && <VerifiedIcon />}
            >
              {loading ? (
                <CircularProgress size={22} sx={{ color: "#fff" }} />
              ) : (
                "Verify OTP"
              )}
            </Button>
            <Button
              fullWidth
              variant="text"
              sx={{ mt: 1 }}
              onClick={handleResend}
              disabled={resendLoading}
            >
              {resendLoading ? "Resending..." : "Resend OTP"}
            </Button>
          </CardContent>
        </Card>
      </Grow>
    </Box>
  );
};

export default VerifyEmailOtp;
