import React, { useEffect } from "react";
import {
  Box,
  Card,
  CardContent,
  Typography,
  CircularProgress,
} from "@mui/material";
import { getIrctcAgentStatus } from "../../api/login/Login";

const CheckAgentStatus = ({ onResult }) => {
  useEffect(() => {
    const fetchStatus = async () => {
      try {
        const res = await getIrctcAgentStatus();
        const agentData = res?.data?.data;

        if (agentData?.kyc === true) {
          onResult(true, null);
        } else {
          onResult(false, agentData);
        }
      } catch (err) {
        console.error("IRCTC status error", err);
        onResult(false, null);
      }
    };

    fetchStatus();
  }, [onResult]);

  return (
    <Box
      sx={{
        minHeight: "70vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <Card sx={{ width: 420 }}>
        <CardContent sx={{ textAlign: "center" }}>
          <CircularProgress sx={{ mb: 2 }} />
          <Typography variant="h6">Checking Agent Status</Typography>
          <Typography variant="body2" color="text.secondary">
            Verifying onboarding details
          </Typography>
        </CardContent>
      </Card>
    </Box>
  );
};

export default CheckAgentStatus;
